.App {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  cursor: none;
}

.Project {
  --dimension: 15rem;
  width: var(--dimension);
  height: var(--dimension);
  display: grid;
  place-items: center;
  border: 1px solid white;
  border-radius: 50%;
  overflow: hidden;
  margin: 0;
  padding: 0;
  gap: 1rem;
  transition: background-color 0.5s;
  cursor: pointer;
  font-size: 2rem;
  font-weight: bold;
  color: white;
}

.Project:hover {
  background-color: rgba(40, 255, 255, 0.1);
}
